theme-alert, theme-modal {
  z-index: 9999;
  opacity: 1;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 80%;
  height: 100dvh;
  max-height: 90dvh;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

:is(theme-alert, theme-modal):not([open]) {
  opacity: 0;
  pointer-events: none;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

body:after {
  content: "";
  opacity: 0;
  pointer-events: none;
  z-index: 9000;
  background: #0004;
  width: 100vw;
  height: 100dvh;
  transition: opacity .3s;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

:is(body:has(theme-alert[open]), body:has(theme-modal[open]), body:has(theme-drawer[open])):after {
  pointer-events: none;
  opacity: 1;
}

@keyframes progress {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

theme-notification {
  will-change: transform;
  z-index: 9999;
  background-color: #fff;
  width: min(80vw, 400px);
  transition: transform .3s cubic-bezier(.165, .84, .44, 1);
  display: block;
  position: fixed;
  top: 20%;
  right: 0;
  transform: translateX(0%);
}

theme-notification:not([open]) {
  transform: translateX(100%);
}

theme-modal-button {
  cursor: pointer;
  display: block;
}

theme-drawer {
  z-index: 9999;
  will-change: transform;
  background-color: #fff;
  flex-direction: column;
  width: min(100vw, 400px);
  height: 100dvh;
  transition: transform .3s cubic-bezier(.165, .84, .44, 1);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(0%);
}

theme-drawer:not([open]) {
  transform: translateX(100%);
}
